[data-animation*="ripple"] {
  position: relative;
  overflow: hidden;
}

[data-animation*="ripple"] .ripple-circle {
  position: absolute;
  z-index: 20;
  top: 50%;
  left: 50%;
  border-radius: 50%;
  width: 112px;
  height: 112px;
  background: rgba(0, 0, 0, 0.1);
  animation: ripple 0.5s linear forwards;
  pointer-events: none;
}

[data-animation*="white"] .ripple-circle {
  background: rgba(255, 255, 255, 0.1);
}

@keyframes ripple {
  0% {
    opacity: 1;
    transform: scale(0);
  }
  100% {
    opacity: 0;
    transform: scale(2);
  }
}

.header {
  position: relative;
  color: #fff;
  background: #2e99db;
  text-align: center;
}

.header__inner {
  padding: 0 40px;
  height: 60px;
  text-align: center;
  font-size: 0;
}

.header__inner:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.header__title {
  display: inline-block;
  vertical-align: middle;
  font-size: 28px;
  font-weight: 500;
  line-height: 30px;
  text-transform: uppercase;
}

@media only screen and (max-width: 1023px) {
  .header__title {
    font-size: 24px;
  }
}

@media only screen and (max-width: 767px) {
  .header__title {
    max-width: 200px;
    font-size: 18px;
    line-height: 1.2;
  }
}

.header__logout {
  position: absolute;
  z-index: 1;
  top: 0;
  right: 0;
  bottom: 0;
  width: 120px;
  color: #fff;
  background: none;
  text-align: center;
  font-size: 0;
  cursor: pointer;
  transition: background 0.25s, box-shadow 0.25s;
}

.header__logout:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

@media only screen and (max-width: 1023px) {
  .header__logout {
    width: 60px;
  }
}

.no-touch .header__logout:hover {
  background: #1f7db7;
}

.header__logout:active {
  background: #1f7db7;
  box-shadow: inset 2px 2px 6px rgba(0, 0, 0, 0.2);
}

.header__logout ~ .header__inner {
  padding-left: 120px;
  padding-right: 120px;
}

@media only screen and (max-width: 1023px) {
  .header__logout ~ .header__inner {
    padding-left: 60px;
    padding-right: 60px;
  }
}

.header__logout .icon {
  fill: currentColor;
}

.header__logout .icon,
.header__logout span {
  display: inline-block;
  vertical-align: middle;
  font-size: 18px;
}

.header__logout span {
  padding-left: 10px;
}

@media only screen and (max-width: 1023px) {
  .header__logout span {
    display: none;
  }
}

.btn {
  position: relative;
  overflow: hidden;
  display: inline-block;
  vertical-align: top;
  padding: 0 15px;
  min-width: 120px;
  border: none;
  outline: none;
  color: #fff;
  background: #2e99db;
  font-size: 19px;
  font-weight: 300;
  line-height: 60px;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  transition: opacity .175s, background .175s, box-shadow .175s;
}

.btn:active {
  color: #fff;
  background: #1f7db7;
  box-shadow: inset 2px 2px 6px rgba(0, 0, 0, 0.2);
}

.btn:hover .icon-right {
  transform: translate3d(50%, 0, 0);
}

.btn:hover .icon-left {
  transform: translate3d(-50%, 0, 0);
}

.btn_red {
  background: #ec4a41;
}

.btn_red:active {
  background: #e32117;
}

.btn_wide {
  width: 100%;
}

.btn .icon-right,
.btn .icon-left {
  display: inline-block;
  vertical-align: top;
  fill: currentColor;
  font-size: 80%;
  margin-top: 22px;
  transition: transform .175s;
}

.input {
  padding: 0 15px;
  border: 1px solid #333;
  border-radius: 0;
  width: 100%;
  height: 60px;
  background: #fff;
  box-shadow: none;
  font-size: 16px;
  font-weight: 400;
  line-height: normal;
  appearance: none;
  transition: border-color 0.25s;
}

.input::-ms-clear {
  display: none;
}

.input:focus {
  border-color: #2e99db;
}

.input:disabled {
  border-color: #b3b3b3;
  background: #f2f2f2;
  cursor: not-allowed;
}

.input.is-error {
  border-color: #ec4a41;
}

.input_sm {
  height: 40px;
}

.form-group {
  position: relative;
}

.form-group:not(:last-child) {
  margin-bottom: 20px;
}

.form-group__label {
  display: block;
  margin-bottom: 8px;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.3;
}

.form {
  margin: 0 auto;
}

.form_sm {
  max-width: 400px;
}

.form_box {
  padding: 30px;
  background: #fff;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
}

@media only screen and (max-width: 599px) {
  .form_box {
    padding: 0;
    background: none;
    box-shadow: none;
  }
}

.form__footer {
  padding-top: 10px;
}
